/* ------------------------------------------------------------------------------------------------ */
/* Custom Modal */
/* ------------------------------------------------------------------------------------------------ */

.modal {
  .modal-dialog {}

  .modal-content {
    border: 0;
    border-radius: 0;
    .modal-header {
      background: $light;
      .modal-title {
        font-weight: 600; } }

    .modal-footer {
      .btn {
        margin-bottom: 0; } } } }


.fade-scale {
  transform: scale(0);
  opacity: 0;
  transition: all .3s;
  &.show {
    transform: scale(1);
    opacity: 1; } }
