/* -------------------- */
/* Content */
/* -------------------- */

main {
  min-height: calc(100vh - 90px); }

/* --LOGIN-- */
body.login main {
  padding: 0; }
.auth-page {
  .left-wrap,
  .right-wrap {
    min-height: 100vh;
    position: relative; }
  .image {
    background-size: cover;
    background-position: cover;
    @include absfull; }
  p {
    text-align: center; }

  .form-wrap {
    min-height: 100%;
    width: 340px;
    display: flex;
    align-items: center;
    transform: translateX(-190px);
    .box {
      width: 100%;
      padding: 2rem;
      border-radius: 3px;
      background-color: #ffffff;
      box-shadow: 6px 14px 51px 3px rgba(94, 94, 94, 0.42); }
    .forgot-pw {
      margin-top: .5rem;
      font-size: .9rem;
      line-height: 1.5em;
      text-align: right; }
    .form-group.empw {
      .form-control {
        position: relative;
        &:focus {
          z-index: 2; } }
      .form-control.top {
        border-radius: 3px 3px 0 0; }
      .form-control.bot {
        border-radius: 0 0 3px 3px;
        margin-top: -1px; } } }
  @include responsive(769px) {
    .left-wrap {
      min-height: 40vh; }
    .right-wrap {
      min-height: 60vh; }
    .form-wrap {
      width: 100%;
      transform: translateX(0) translateY(-100px);
      justify-content: center;
      .box {
        width: 60%; } } }
  @include responsive(426px) {
    .form-wrap {
      .box {
        width: 100%; } } } }


.content-wrap {
  margin-left: 240px;
  padding: 2rem;
  transition: margin .3s;
  @include responsive(0,1600px) {
    margin-left: 300px; }
  @include responsive(426px) {
    margin-left: 40px; } }


.content-header {
  border-bottom: 1px solid $border;
  margin-bottom: 2rem;
  .title {
    margin-bottom: 1rem;
    display: inline-block; }
  .right-content {
    float: right;
    .btn {
      margin-bottom: 0; } }
  @include responsive(426px) {
    .btn-add {
      &:before {
        content: "\f067";
        font-family: FontAwesome; }
      span {
        display: none; } } } }


.filter,
.bulk-action {
  @include responsive(426px) {
    margin-bottom: .5rem; } }


.search {
  position: relative;
  .btn.fa-search {
    position: absolute;
    right: 0;
    top: 0;
    color: $text-light;
    padding: 0 .8rem;
    &:hover,
    &:focus {
      text-decoration: none; } } }



.page-not-found {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: center;
  .img {
    position: relative;
    max-width: 100%;
    padding: 2rem;
    img {
      max-width: 100%; } }
  .text {
    text-align: center;
    color: $primary; } }




ul.pagination {
  display: inline-block;
  margin-bottom: 0;
  li {
    display: inline-block;
    margin-bottom: 0;
    padding: 5px 0;
    a {
      display: inline-block;
      text-align: center;
      padding: 0 .5rem;
      line-height: 30px;
      min-width: 30px;
      color: $text;
      border-radius: 2px;
      &:hover {
        background-color: $primary;
        text-decoration: none;
        color: $white; } } }
  @include responsive(426px) {
    width: 100%;
    margin-top: .5rem;
    text-align: center; } }
