// Responsive
@mixin responsive($max:0, $min: 0) {
  @if $min == 0 {
    @media(max-width: $max) {
      @content; } }
  @else if $max == 0 {
    @media(min-width: $min) {
      @content; } }
  @else {
    @media(max-width: $max) and  (min-width: $min) {
      @content; } } }

// Small devices (mobile phones, less than 576px)
@mixin mobile() {
  @include responsive(767.98px); }

// Medium devices (tablets, less than 992px)
@mixin tablet() {
  @include responsive(991.98px); }

// Large devices (small desktops, less than 1200px)
@mixin sdesktop() {
  @include responsive(1199.98px); }



// Clear fix
@mixin clearfix() {
  display: block;
  clear: both;
  content: ""; }

// Absolute full
@mixin absfull() {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

//allstate
@mixin allstate() {
  &:hover,
  &:focus,
  &:not(:disabled):not(.disabled):active,
  &:active:focus {
    @content; } }
