/* ------------------------------------------------------------------------------------------------ */
/* Fonts */
/* ------------------------------------------------------------------------------------------------ */

@mixin fontGenerator($name,$weight,$family) {
  @for $i from 1 through length($family) {
    $src: #{"../fonts/"}#{nth($family, $i)};
    @font-face {
      font-family : $name;
      src         : local($name);
      src         : url("#{$src}.eot");
      src         : url("#{$src}.eot#iefix") format("embedded-opentype"), url("#{$src}.ttf") format("truetype"), url("#{$src}.woff") format("woff");
      font-weight : nth($weight, $i); } } }


@include fontGenerator("Roboto", (200, 400, 600, 800), (roboto-light, roboto-regular, roboto-medium, roboto-bold));
