/* -------------------- */
/* Base */
/* -------------------- */

*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html,
body {
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  line-height: 1.5em;
  font-weight: 400;
  margin: 0px;
  color: $text;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased; }

a {
  color: $link;
  &:hover {
    color: $link;
    tex-decoration: underline; } }

hr {
  border-top: 1px solid $border;
  margin: 1.5rem 0; }

/* -------------------- */
/* Typography */
/* -------------------- */

h1, h2, h3, h4, h5, h6 {
  font-weight: 200;
  line-height: 1.5em;
  margin: 0 0 30px;
  small {
    font-size: 60%;
    font-weight: 200;
    @include responsive(426px) {
      display: block;
      width: 100%; } } }

h1 {
  font-size: 3.3125rem;
  line-height: 1.15em; }

h2 {
  font-size: 2.25rem;
  line-height: 1.5em; }

h3 {
  font-size: 1.5625rem;
  line-height: 1.4em; }

h4 {
  font-size: 1.125rem;
  line-height: 1.5em; }

h5 {
  font-size: 1.0625rem;
  line-height: 1.55em; }

h6 {
  font-size: .75rem;
  font-weight: 600;
  text-transform: uppercase; }

p {
  margin: 0 0 30px;
  padding: 0;
  line-height: 1.5em;
  .lead {
    font-weight: 400; } }

ul,
ol {
  padding: 0 0 0 25px;
  margin: 0 0 30px;
  li {
    margin-bottom: .5em;
    ul, ol {
      margin: .5em 0; } } }

.blockquote {
  font-size: 1.5625rem;
  line-height: 1.5em;
  font-weight: 200;
  font-style: italic;
  text-align: justify;
  position: relative;
  margin-bottom: 30px;
  .blockquote-footer {
    font-size: 1rem;
    font-weight: 200;
    font-style: normal; } }
