/* ------------------------------------------------------------------------------------------------ */
/* General */
/* ------------------------------------------------------------------------------------------------ */

.logo {
  font-size: 1.8rem;
  line-height: 1.5em;
  font-weight: 600;
  color: $primary;
  text-align: center;
  img {
    width: 40px;
    height: auto;
    margin-right: .5rem; }
  a:hover {
    color: $primary;
    text-decoration: none; } }

.close {
  font-weight: 200;
  text-shadow: none;
  font-size: 1.5rem;
  opacity: 1;
  transition: opacity .5s;
  @include allstate {
    opacity: .5;
    color: inherit;
    Outline: none; } }

.btn {
  position: relative;
  border-radius: 2px;
  padding: 0 25px;
  line-height: 38px;
  font-weight: 400;
  &:focus,
  &:not(:disabled):not(.disabled):active:focus {
    box-shadow: none; }
  &:active,
  &:active:focus {
    box-shadow: inset 0px 2px 10px 0px rgba(0,0,0,0.15) !important; }

  // Button Colors
  &.btn-primary {
    color: $white;
    background-color: $primary;
    border-color: $primary;
    @include allstate {
      color: $white !important;
      background-color: darken($primary, 5%) !important;
      border-color: darken($primary, 5%) !important; } }
  &.btn-secondary {
    color: $white;
    background-color: $secondary;
    border-color: $secondary;
    @include allstate {
      color: $white;
      background-color: darken($secondary, 5%) !important;
      border-color: darken($secondary, 5%) !important; } }
  &.btn-success {
    color: $white;
    background-color: $success;
    border-color: $success;
    @include allstate {
      color: $white !important;
      background-color: darken($success, 5%) !important;
      border-color: darken($success, 5%) !important; } }
  &.btn-danger {
    color: $white;
    background-color: $danger;
    border-color: $danger;
    @include allstate {
      color: $white !important;
      background-color: darken($danger, 5%) !important;
      border-color: darken($danger, 5%) !important; } }
  &.btn-warning {
    color: $white;
    background-color: $warning;
    border-color: $warning;
    @include allstate {
      color: $white !important;
      background-color: darken($warning, 5%) !important;
      border-color: darken($warning, 5%) !important; } }
  &.btn-info {
    color: $white;
    background-color: $info;
    border-color: $info;
    @include allstate {
      color: $white !important;
      background-color: darken($info, 5%) !important;
      border-color: darken($info, 5%) !important; } }
  &.btn-light {
    color: $text;
    background-color: $light;
    border-color: $light;
    @include allstate {
      color: $text !important;
      background-color: darken($light, 5%) !important;
      border-color: darken($light, 5%) !important; } }
  &.btn-dark {
    color: $white;
    background-color: $dark;
    border-color: $dark;
    @include allstate {
      color: $white !important;
      background-color: darken($dark, 5%) !important;
      border-color: darken($dark, 5%) !important; } }
  &.btn-link {
    font-weight: 400;
    color: $link; }

  // Button Outline
  &.btn-outline-primary {
    color: $primary;
    border-color: $primary;
    @include allstate {
      color: $white !important;
      background-color: darken($primary, 5%) !important;
      border-color: darken($primary, 5%) !important; } }
  &.btn-outline-secondary {
    color: $text-light;
    border-color: $secondary;
    @include allstate {
      color: $white;
      background-color: darken($secondary, 5%) !important;
      border-color: darken($secondary, 5%) !important; } }
  &.btn-outline-success {
    color: $success;
    border-color: $success;
    @include allstate {
      color: $white !important;
      background-color: darken($success, 5%) !important;
      border-color: darken($success, 5%) !important; } }
  &.btn-outline-danger {
    color: $danger;
    border-color: $danger;
    @include allstate {
      color: $white !important;
      background-color: darken($danger, 5%) !important;
      border-color: darken($danger, 5%) !important; } }
  &.btn-outline-warning {
    color: $warning;
    border-color: $warning;
    @include allstate {
      color: $white !important;
      background-color: darken($warning, 5%) !important;
      border-color: darken($warning, 5%) !important; } }
  &.btn-outline-info {
    color: $info;
    border-color: $info;
    @include allstate {
      color: $white !important;
      background-color: darken($info, 5%) !important;
      border-color: darken($info, 5%) !important; } }
  &.btn-outline-light {
    color: $light;
    border-color: $light;
    @include allstate {
      color: $text !important;
      background-color: darken($light, 5%) !important;
      border-color: darken($light, 5%) !important; } }
  &.btn-outline-dark {
    color: $dark;
    border-color: $dark;
    @include allstate {
      color: $white !important;
      background-color: darken($dark, 5%) !important;
      border-color: darken($dark, 5%) !important; } }

  // Button Style
  &.btn-round {
    border-radius: 20px; }
  &.btn-wicon {
    padding: 0 15px;
    .fa {
      margin-right: 10px; } }
  &.btn-icon {
    width: 40px;
    padding: 0; }

  // Button size
  &.btn-sm {
    line-height: 28px; }
  &.btn-lg {
    line-height: 48px; }


 }  // Ripple effect
.btn {
  background-position: center;
  overflow: hidden;
  &:not(.btn-link):after {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.1) radial-gradient(circle, transparent 1%, rgba(0,0,0,0.1) 1%) center/15000%;
    opacity: 0;
    transition: all .8s;
    @include absfull; }
  &:not(.btn-link):hover:after {
    opacity: 1; }
  &:not(.btn-link):active:after {
    background-size: 100%;
    transition: all 0s; } }



.alert {
  border-radius: 0;
  border: 0;
  padding: 1rem 60px 1rem 1.5rem;
  margin: 0 0 30px;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 400;
  .lead {
    font-size: 90%;
    font-weight: 600;
    text-transform: uppercase;
    margin-right: .5rem;
    &:after {
      content: ":"; } }
  .close {
    padding: 1rem 1.5rem;
    background: transparent;
    border: 0;
    -webkit-appearance: none;
    span {
      display: block;
      transform: translateY(-2px); } }
  &.alert-info {
    color: $white;
    background-color: $info; }
  &.alert-success {
    color: $white;
    background-color: $success; }
  &.alert-warning {
    color: $white;
    background-color: $warning; }
  &.alert-danger {
    color: $white;
    background-color: $danger; } }



.badge {
  border-radius: 2px;
  font-weight: 200;
  padding: .3rem .5rem;
  margin: 0 .3rem;
  &:first-child {
    margin-left: 0; }
  &:last-child {
    margin-right: 0; }
  &:first-child:last-child {
    margin-left: 0;
    margin-right: 0; } }
.badge-primary {
  color: $white;
  background-color: $primary; }
.badge-secondary {
  color: $white;
  background-color: $secondary; }
.badge-success {
  color: $white;
  background-color: $success; }
.badge-danger {
  color: $white;
  background-color: $danger; }
.badge-warning {
  color: $white;
  background-color: $warning; }
.badge-info {
  color: $white;
  background-color: $info; }
.badge-light {
  color: $text;
  background-color: $light; }
.badge-dark {
  color: $white;
  background-color: $dark; }



.breadcrumb {
  border-radius: 0;
  padding: 1rem 1.5rem;
  background-color: $light; }
.breadcrumb-item {
  margin-bottom: 0;
  font-weight: 200;
  a {
    opacity: 1;
    transition: opacity .5s; }
  a:hover {
    text-decoration: none;
    opacity: .7; }
  &.active {
    color: $text-light; }
  &+ .breadcrumb-item:before {
    content: "â€º";
    font-size: 1.2rem;
    line-height: 1; } }



.card {
  border: 0;
  border-radius: 0;
  margin-bottom: 3rem;
  .card-img {
    border-radius: 0;
    width: 100%;
    padding-top: 60%;
    position: relative;
    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
      @include absfull; } }
  .card-body {
    padding: 1.5rem 0;
    .card-title {
      font-size: 1.5625rem;
      line-height: 1.4em;
      margin-bottom: 1rem;
      a {
        color: $text;
        &:hover {
          color: $primary;
          text-decoration: none; } } }
    *:last-child {
      margin-bottom: 0; }
    &:first-child {
      padding-top: 0; }
    &:last-child {
      padding-bottom: 0; }
    &:first-child:last-child {
      padding-top: 0;
      padding-bottom: 0; } } }



.dropdown {
  .dropdown-menu {
    border-radius: 3px;
    padding: .2rem 0;
    min-width: 10rem; }
  .dropdown-divider {
    margin: .2rem 0; }
  .dropdown-item {
    font-size: .9rem;
    &:active {
      background-color: $primary; } } }

.dropdown-menu ul::-webkit-scrollbar-track {
  -webkit-box-shadow: none;
  background-color: transparent;
  border-radius: 8px; }

.dropdown-menu ul::-webkit-scrollbar {
  width: 8px;
  background-color: transparent; }

.dropdown-menu ul::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: $border;
  border: 2px solid #FFF; }


.multiplier {
  margin-bottom: 1.5rem;
  position: relative;
  &.multiplied {
    .item-header {
      display: block; } }
  .item-header {
    display: none;
    .title {
      font-weight: 600;
      margin-bottom: .5rem;
      display: inline-block; }
    .btn-del {
      margin: 0;
      float: right;
      font-size: .75rem;
      line-height: 1.5em;
      opacity: .7;
      transition: all .3s;
      padding: 0 .5rem;
      &:hover {
        opacity: 1; } }
    &:before {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background-color: $border;
      margin-bottom: .5rem; } }
  .btn-add {
    width: 100%;
    height: 1px;
    background-color: $border;
    margin: 5px 0 15px;
    position: relative;
    cursor: pointer;
    transition: all .3s;
    &:after {
      content: "\f067";
      font-family: FontAwesome;
      color: #fff;
      text-align: center;
      display: block;
      background-color: $secondary;
      width: 30px;
      height: 30px;
      line-height: 30px;
      border: 0;
      border-radius: 15px;
      position: absolute;
      top: -15px;
      left: calc(50% - 15px);
      transition: all .3s; }
    &:hover:after {
      background-color: $primary; } } }

dl {
  dt,
  dd {
    font-size: 1rem;
    line-height: 1.75em;
    margin-bottom: 1.5rem;
    .title {
      font-size: 1.125rem;
      line-height: 1.5em;
      margin-bottom: 0; } }
  dt {
    h1,h2,h3,h4,h5,h6 {
      margin-bottom: 0; }
    @include responsive(426px) {
      margin-bottom: .5rem; } } }
