/* ------------------------------------------------------------------------------------------------ */
/* Header */
/* ------------------------------------------------------------------------------------------------ */

main {
  padding-top: 60px; }

header {
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 60px;
  color: #fff;
  background-color: $primary;
  padding: 0 2rem;
  transition: all .3s;
  .logo {
    margin: 15px 0 0 35px;
    font-size: 1.3125rem;
    text-align: left;
    transition: all .3s;
    position: absolute;
    left: 35px;
    top: 0;
    a {
      color: #ffffff;
      &:hover {
        color: #ffffff; } }
    @include responsive(0,1600px) {
      margin-left: 45px; } }
  .account {
    float: right;
    display: flex;
    padding: 10px 0 0;
    cursor: pointer;
    .image {
      height: 40px;
      width: 40px;
      border-radius: 50%;
      overflow: hidden;
      img {
        width: 40px;
        height: 40px;
        object-fit: cover; } }
    .content {
      padding-left: 1rem;
      span {
        display: block; }
      .role {
        font-size: .75rem;
        line-height: 1.5em; } }
    &:after {
      content: "\f107";
      display: block;
      padding-left: .5rem;
      font-family: FontAwesome; } }
  &.hide {
    transform: translateY(-80px); }
  &.sticky {
    transform: translateY(0); }

  @include responsive(426px) {
    .account {
      .content {
        display: none; }
      &:after {
        display: none; } } } }


nav.main-menu {
  position: fixed;
  z-index: 101;
  top: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  width: 240px;
  background-color: #fff;
  border-right: 1px solid #f2f2f2;
  box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.08);
  transition: all .3s;
  @include responsive(0,1600px) {
    width: 300px; }
  .logo {
    margin: 1rem;
    font-size: 1.3125rem;
    text-align: left;
    transition: all .3s;
    img {
      width: 30px;
      height: 30px; } }
  .menu-toggle {
    position: absolute;
    right: 0;
    top: calc(50% - 25px);
    padding: 10px;
    &:after {
      content: "";
      display: block;
      width: 5px;
      height: 50px;
      border-radius: 5px;
      background-color: $secondary;
      z-index: 10;
 }      // transition: all .3s
    &:hover {
      cursor: pointer;
      &:after {
        background-color: $primary; } } }
  ul.menu {
    list-style: none;
    margin: 3rem 0;
    padding: 0;
    transition: all .3s;
    li {
      padding: .5rem 1rem;
      margin: 0;
      position: relative;
      transition: all .3s;
      a {
        color: $text-light;
        .fa {
          width: 1.5rem;
          text-align: center;
          margin-right: .5rem; }
        .fa-gear {
          font-size: 1.2rem; } }
      &:hover {
        a {
          color: $primary;
          text-decoration: none; } }
      &.active {
        a {
          color: $primary; }
        &:before {
          content: "";
          display: block;
          width: 5px;
          height: calc(100% - 6px);
          position: absolute;
          top: 3px;
          left: 0;
          background-color: $primary; } } } } }


// COLLAPSE MENU
body.menu-collapse {
  nav.main-menu {
    transform: translateX(-200px);
    .logo {
      transform: translateX(-200px); }
    .menu-toggle {
      width: 40px;
      height: 60px;
      background-color: transparent;
      top: 0;
      right: 0;
      padding: 0;
      text-align: center;
      &:before {
        content: "\f101";
        font-family: FontAwesome;
        font-size: 1.3rem;
        line-height: 60px;
        color: $secondary; }
      &:after {
        display: none; }
      &:hover {
        background-color: $light;
        color: $primary; } }
    ul.menu {
      transform: translateX(200px);
      width: 40px;
      li {
        padding: .5rem 0 .5rem 5px;
        text-align: center;
        a {
          .fa {
            margin: 0; }
          span {
            display: none; } } } }
    @include responsive(0,1600px) {
      transform: translateX(-250px);
      .logo, {
        transform: translateX(-250px); }
      .menu-toggle {
        width: 50px; }
      ul.menu {
        transform: translateX(250px); } } }

  .content-wrap {
    margin-left: 40px;
    @include responsive(0,1600px) {
      margin-left: 50px; } } }
