/* ------------------------------------------------------------------------------------------------ */
/* Form */
/* ------------------------------------------------------------------------------------------------ */

label {
  font-size: 1rem;
  line-height: 1.5em;
  margin-bottom: .5em; }

.form-control {
  font-size: 1rem;
  border-radius: 2px;
  border: 1px solid $secondary;
  &:not(textarea) {
    height: 40px; }
  &:focus,
  &:active,
  &:focus:active {
    border-color: $primary;
    box-shadow: none !important;
    outline: none !important; }
  &::placeholder {
    color: #ccc; }
  &[readonly] {
    background-color: $light; } }

.checkbox,
.radio {
  position: relative;
  display: block;
  min-height: 1.5rem;
  padding-left: 1.8rem;
  text-align: left;
  input {
    position: absolute;
    z-index: -1;
    opacity: 0; }
  label {
    margin-bottom: 0;
    cursor: pointer;
    &:before, {
      content: "";
      background-color: $white;
      border: 1px solid $secondary;
      border-radius: 2px;
      display: block;
      position: absolute;
      top: .1rem;
      left: 0;
      width: 1.3rem;
      height: 1.3rem;
      transition: all .3s; }
    &:after {
      content: "\f00c";
      font-size: 1rem;
      line-height: 1.2rem;
      text-align: center;
      font-family: FontAwesome;
      display: block;
      position: absolute;
      top: .1rem;
      left: 0;
      width: 1.3rem;
      height: 1.3rem;
      color: $white;
      transform: scale(0);
      transform: {}
      transition: all .3s; } }

  input {
    &:checked + label:before {
      border-color: $primary; }
    &:checked + label:after {
      transform: scale(1); } } }

.checkbox {
  input:checked + label:before {
    background-color: $primary; } }

.radio {
  label {
    &:before {
      border-radius: 1rem; }
    &:after {
      content: "";
      border-radius: 1rem;
      background-color: $primary;
      top: .3rem;
      left: .2rem;
      width: .9rem;
      height: .9rem; } } }

.checkbox + .checkbox {
  margin-top: .5rem; }
.radio + .radio {
  margin-top: .5rem; }

.select-control {
  .btn {
    border-color: $secondary;
    color: $text;
    background-color: $white;
    &:hover {
      background-color: $light; } }
  .dropdown-menu.open {
    border-color: $secondary; } }

.date-control {
  position: relative;
  input {
    padding-right: 40px;
    user-select: none;
    &:focus + label {
      color: $primary; } }
  .form-control[readonly] {
    background-color: #fff; }
  label {
    position: absolute;
    top: 0;
    right: 0;
    margin: 0;
    line-height: 40px;
    min-width: 40px;
    text-align: center;
    cursor: pointer;
    color: $text-light;
    &:hover {
      color: $primary; } } }

.inputfile {
  input[type="file"] {
    position: absolute;
    z-index: -1;
    opacity: 0; } }

.form-group {
  margin-bottom: 1.5rem; }

.form-action {
  text-align: right;
  .btn {
    margin-bottom: 0;
    min-width: 120px;
    &+ .btn {
      margin-left: 1rem; } } }

.input-group {
  .input-group-text {
    border-color: $secondary;
    background-color: #fff; }
  .input-group-prepend {
    .input-group-text {
      border-radius: 2px 0 0 2px; } }
  .input-group-append {
    .input-group-text {
      border-radius: 0 2px 2px 0;
      border-left-color: transparent; } }
  &.focused {
    .input-group-text {
      border-color: $primary; } }
  &.readonly {
    .input-group-text {
      background-color: $light; } }
  &>.form-control:not(:last-child) {
    border-right: 0;
    padding-right: 0; }
  &>.form-control:not(:first-child) {
    border-left: 0;
    padding-left: 0; } }
