.btn-group.bootstrap-select {
  position: relative;
  width: 100%;

  .btn {
    position: relative;
    margin: 0;
    padding: 0 .75rem;
    font-size: 1rem;
    font-weight: normal;
    width: 100%;
    &:after {
      display: none; }
    &.bs-placeholder {
      span {
        color: #999999; } }
    .bs-caret {
      position: absolute;
      right: .75rem;
      top: 0;
      .caret {
        &:before {
          content: "\f107";
          font-family: FontAwesome; } } } }

  // RESET BS 4 style dopdown style
  .dropdown-menu {
    position: relative;
    top: auto;
    left: auto;
    z-index: 1000;
    display: block;
    float: none;
    min-width: auto;
    padding: 0;
    margin: 0;
    font-size: inherit;
    color: inherit;
    text-align: left;
    list-style: none;
    background-color: inherit;
    background-clip: initial;
    border: 0;
    border-radius: 0; }

  .open {
    min-width: 100%;
    position: absolute;
    left: 0;
    background: #ffffff;
    border: 1px solid #e2e2e2;
    border-radius: 2px;
    margin: 2px 0;
    max-height: 185px !important;
    display: none;
    overflow-y: auto;
    ul.dropdown-menu.inner {
      margin: 0;
      padding: .5rem 0;
      max-height: 185px !important;
      overflow-y: auto;
      li {
        margin: 0;
        position: relative;
        a {
          font-size: .9rem;
          line-height: 1.5em;
          display: block;
          padding: 6px 15px;
          color: #212121;
          background: #ffffff;
          transition: background .3s;
          &:hover {
            cursor: pointer;
            text-decoration: none;
            background: #f2f2f2; } }
        &.selected a,
        &.active a {
          background: #f2f2f2; }
        &.hidden {
          display: none; }
        &.no-results {
          padding: 0 .5rem;
          font-size: .9rem; } } }
    .bs-searchbox + ul.dropdown-menu.inner {
      max-height: 140px !important; }

    * {
      outline: none !important; }
    &.show {
      display: block; } }

  .check-mark {
    color: #888888;
    float: right;
    line-height: 1.5em;
    transform: scale(0);
    transition: transform .3s; }

  select {
    position: absolute;
    z-index: -1;
    opacity: 0; }


  // MULTIPLE
  &.show-tick {
    ul.dropdown-menu.inner {
      li.selected {
        .check-mark {
          transform: scale(1); } } } }

  // LIVE SEARCH
  .bs-searchbox {
    padding: .5rem .5rem 0;
    input {
      height: 36px; } } }
