table.table-custom {
  background-color: #ffffff;
  margin-bottom: 1.5rem;
  thead {
    tr {
      th {
        text-align: center;
        font-weight: 400;
        font-size: 0.875rem;
        line-height: 1.4em;
        font-weight: 600;
        text-transform: uppercase;
        border: 0;
        color: $text;
        background-color: #e9ecef;
        padding: .7rem 1rem;
        border: 1px solid $white;
        &:first-child {
          border-left: 0; }
        &:last-child {
          border-right: 0; }
        .checkbox {
          padding-left: 1.3rem; } } } }
  tbody {
    tr {
      td {
        padding: .75rem 1rem;
        vertical-align: middle;
        border-bottom: 1px solid $border;
        .checkbox {
          padding-left: 1.3rem; } } } }


  .btn {
    margin-bottom: 0; }
  .btn-light {
    color: $text-light;
    border-color: $text-light; }
  .status {
    min-width: 100px;
    border-radius: 20px; } }
